import * as React from 'react'
import { Link, graphql } from 'gatsby'
import { useBreakpoint } from 'gatsby-plugin-breakpoints'
import Helmet from 'react-helmet'
import styled from 'styled-components'
import PropTypes from 'prop-types'
import '../components/layout.css'
import ContentWrap from '../components/contentWrap'
import GlobalNavigation from '../components/globalNavigation'
import Header from '../components/header'
import Hero from '../components/hero'
import DownloadMochi from '../components/downloadMochi'
import Marquee from '../components/marquee'
import AccessCounter from '../components/accessCounter'
import DaysFromFoundation from '../components/daysFromFoundation'
import RandomWord from '../components/randomWord'
import Information from '../components/information'
import Footer from '../components/footer'

const Container = styled.div`
  margin: 0 auto;
  max-width: 1080px;
`

const MainContents = styled.div`
  padding: 30px 30px 60px;
`

const CatchTextWrap = styled.div`
  background-color: #ffa927;
  box-shadow: 6px 6px rgb(0, 0, 0, 0.75);
  padding: 20px;
  p {
    margin: 0;
    text-align: center;
    font-size: 22px;
    line-height: 1.8;
  }
`

const ComponentWrap = styled.div`
  margin: 30px 0;
`

const FlexWrap = styled.div.attrs(
  (props: IndexPageProps) => ({
    isMobile: props.isMobile,
  })
)`
  display: flex;
  justify-content: space-around;
  flex-direction: ${(props) =>
    props.isMobile ? 'column' : 'row'};
  > * {
    margin: 16px;
  }
`

interface IndexPageProps {
  data: {
    site: {
      siteMetadata: {
        title: string
        description: string
      }
    }
  }
  isMobile: boolean
}

const IndexPage: React.FC<IndexPageProps> = ({ data }) => {
  const breakpoints = useBreakpoint()

  return (
    <div>
      <Helmet
        title={data.site.siteMetadata.title}
        meta={[
          {
            name: 'description',
            content: data.site.siteMetadata.description,
          },
        ]}
      />
      <Container>
        <Header />
        <Hero />
        <DownloadMochi />
        <GlobalNavigation />
        <ContentWrap>
          <main>
            <Marquee text="BAD" />
            <MainContents>
              <CatchTextWrap>
                <p>
                  BAD
                  <br />
                  BAD
                  <br />
                  
                </p>
              </CatchTextWrap>
              <ComponentWrap>
                <FlexWrap isMobile={breakpoints.sm}>
                  <AccessCounter />
                  <DaysFromFoundation />
                </FlexWrap>
              </ComponentWrap>
              <ComponentWrap>
                <RandomWord />
              </ComponentWrap>
              <ComponentWrap>
                <Information />
              </ComponentWrap>
              <Link to="/page-2/">Go to page 2</Link>
            </MainContents>
          </main>
        </ContentWrap>
        <Footer />
      </Container>
    </div>
  )
}

IndexPage.propTypes = {
  data: PropTypes.object,
  'data.site': PropTypes.object,
  'data.site.siteMetadata': PropTypes.object,
  'data.site.siteMetadata.title': PropTypes.string,
  'data.site.siteMetadata.description': PropTypes.string,
} as React.WeakValidationMap<IndexPageProps>

export default IndexPage

export const pageQuery = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        title
        description
      }
    }
  }
`
